import React, { useState, useEffect } from 'react'
import './styles.css'
import Colors from './components/Colors'
import Modal from './components/Modal'
import editIcon from './imgs/plus.svg'
import draw, { drawCharts, changeGradient } from './d3'
import * as d3 from 'd3'
import {colors as colorValues} from './config'

function App() {
  const [colors, setColors] = useState([0, colorValues.length])
  const [isRectsModalOpen, setIsRectsModalOpen] = useState(false)
  const [isGradientModalOpen, setIsGradientModalOpen] = useState(false)
  const [isMousePressed, setIsMousePressed] = useState(false)
  const [countRects, setCountRects] = useState([0, 0])
  const [gradientDirection, setGradientDirection] = useState('bottom-right')
  const [isOverlayOpen, setIsOverlayOpen] = useState(false)
  const [scales, setScales] = useState(null)

  const colorsChange = (top, bottom, change) => {
    setColors([top, bottom])
    changeGradient(colorValues.slice(top, bottom), gradientDirection)
  }

  const onRectsSubmit = e => {
    e.preventDefault();
    setIsOverlayOpen(true);
    const formData = new FormData(e.target.parentNode)
    setCountRects([parseInt(formData.get('horizontal')), parseInt(formData.get('vertical'))])
  }
  const onGradientDirectionSubmit = e => {
    e.preventDefault();
    const formData = new FormData(e.target.parentNode)
    setGradientDirection(formData.get('direction'))
    changeGradient(colorValues.slice(colors[0], colors[1]), formData.get('direction'))
  }

  useEffect(() => {
    const scales = drawCharts()
    setScales(scales)
    
    const canvasSizes = document.getElementById('canvas').getBoundingClientRect()

    const id = 'rect' + Date.now()
    d3.select('#rectsG').append('g')
      .attr('id', id)
      .attr('class', 'rect')
    const x1 = scales[0].invert(window.innerWidth / 2 - 150) - 40 - ((window.prevZoom && window.prevZoom.transform.x) || 0)
    const y1 = canvasSizes.height - 60 - scales[1].invert(window.innerHeight / 2 - 150) - ((window.prevZoom && window.prevZoom.transform.y) || 0)

    setIsOverlayOpen(false)
    draw(
      3,
      3,
      x1,
      y1,
      300,
      300,
      colorValues.slice(colors[0], colors[1]),
      id,
      gradientDirection,
    )
  }, [])

  useEffect(() => {
    if (!(scales && scales.length)) return
    const canvasSizes = document.getElementById('canvas').getBoundingClientRect()

    const overlayDrag = d3.drag()
      .on("end", function () {
        
        const x1 = scales[0].invert(d3.event.subject.x) - 40 - ((window.prevZoom && window.prevZoom.transform.x) || 0)
        const y1 = canvasSizes.height - 60 - scales[1].invert(d3.event.subject.y) - ((window.prevZoom && window.prevZoom.transform.y) || 0)

        const id = 'rect' + Date.now()
        d3.select('#rectsG').append('g')
          .attr('id', id)
          .attr('class', 'rect')

        setIsOverlayOpen(false)
        draw(
          ...countRects, 
          x1,
          y1,
          (d3.event.x - d3.event.subject.x) / ((window.prevZoom && window.prevZoom.transform.k) || 1),
          (d3.event.y - d3.event.subject.y) / ((window.prevZoom && window.prevZoom.transform.k) || 1),
          colorValues.slice(colors[0], colors[1]),
          id,
          gradientDirection,
        )
      });
    d3.select("#overlay").call(overlayDrag);
    
  }, [scales, countRects, gradientDirection])

  return (
    <main>
      <button onClick={() => setIsRectsModalOpen(true)} className='edit'><img src={editIcon} alt='Редактировать' /></button>
      {isRectsModalOpen && 
        <Modal
          onClose={() => setIsRectsModalOpen(false)}
          onSubmit={onRectsSubmit}
          header={'Прямоугольников'}
          form={<ul>
            <li>
               <label htmlFor="horizontal">По горизонтали</label>
               <input type="number" placeholder='2' id='horizontal' min='1' name='horizontal'/>
            </li>
            <li>
               <label htmlFor="vertical">По вертикали</label>
               <input type="number" placeholder='2' id='vertical' min='1' name='vertical'/>
            </li>
         </ul>}
        />
      }
      {isGradientModalOpen && 
        <Modal
          onClose={() => setIsGradientModalOpen(false)}
          onSubmit={onGradientDirectionSubmit}
          header={'Направление градента'}
          form={<ul>
            <li>
              <input type="radio" id="bottom-right" name="direction" value="bottom-right" defaultChecked={gradientDirection === 'bottom-right'}/>
              <label for="huey">-x+y</label>
            </li>
            <li>
              <input type="radio" id="top-right" name="direction" value="top-right" defaultChecked={gradientDirection === 'top-right'}/>
              <label for="huey">-x-y</label>
            </li>
            <li>
              <input type="radio" id="top-left" name="direction" value="top-left" defaultChecked={gradientDirection === 'top-left'}/>
              <label for="huey">x-y</label>
            </li>
            <li>
              <input type="radio" id="bottom-left" name="direction" value="bottom-left" defaultChecked={gradientDirection === 'bottom-left'}/>
              <label for="huey">x+y</label>
            </li>
            <li>
              <input type="radio" id="center" name="direction" value="center" defaultChecked={gradientDirection === 'center'}/>
              <label for="huey">x<sup>2</sup>+y<sup>2</sup></label>
            </li>
         </ul>}
        />
      }
      <div className='wrapper'>
        <div
          id='overlay'
          style={{display: isOverlayOpen ? 'block' : 'none'}}
          // onMouseDown={onMouseDown}
          // onMouseMove={onMouseMove}
          // onMouseUp={onMouseUp}
        ></div>
        <div
          className='canvas'
          id='canvas'
        >
          {/* <div
            id="chart"
            style={{
              left: coords[0][0],
              top: coords[0][1],
              width: coords[1][0] - coords[0][0],
              height: coords[1][1] - coords[0][1],
              background: `linear-gradient(${getGradientAngle(coords)}deg, ${colorValues.slice(colors[0], colors[1]).join(', ')})`
            }}
          ></div> */}
        </div>
      </div>
      <Colors onChange={colorsChange} top={colors[0]} bottom={colors[1]} openGradientModal={() => setIsGradientModalOpen(true)}/>
    </main>
  )
}

export default App
